* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    /* position: absolute; */
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 280px;
    min-height: 100vh;
    background: linear-gradient(93.69deg, #00C5F9 2.23%, #0083DC 96.2%);
    padding: 2rem 1rem 1rem 1rem;
    transition: transform 1s;
}

.navbar {
    max-width: 10px;
    padding: 0;
}

.nav-btn {
    position: absolute;
    transform: translateX(50px);
    top: 20px;
    /* right: 25px; */
    width: 50px;
    /* height: 50px; */
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(93.69deg, #00C5F9 2.23%, #0083DC 96.2%);
    box-shadow: 4px 0px 15px 3px rgba(0, 0, 0, 0.13);
    outline: none;
    border: none;
    font-size: 30px;
    line-height: 36px;
    padding: 5px 10px 8px;
    cursor: pointer;
    color: #FFF;
    border-radius: 50%;
    z-index: 3;
}

.logo {
    display: block;
    width: 100px;
    margin: auto;
    background: transparent;
}

.logo img {
    display: block;
    width: 100%;
    height: 100%;
}

.nav-link {
    display: flex;
    align-items: center;
    color: #fff;
    text-decoration: none;
    padding: 19px 0;
    border-radius: 5px;
    margin-bottom: 5px;
}

.nav-link span {
    margin-left: 20px;
}

.nav-top {
    width: 100%;
    max-width: 165px;
    margin: 60px auto 0;
}

.nav-top .nav-link.active {
    background: #fff;
    border-radius: 5px;
    color: #28A7E8;
    justify-content: center;
}

.nav-top .nav-link span {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
}

.nav-top .nav-link.active span {
    margin-left: 12px;
    color: #28A7E8;
}

.mobile-nav {
    background-color: #753ffd;
    width: 100%;
    height: 40px;
    display: none;
}

.mobile-nav-btn {
    color: #fff;
    background: transparent;
    outline: none;
    border: none;
    margin: 0 10px;
}

.logout {
    display: flex;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    text-decoration: none;
    cursor: pointer;
    align-items: center;
    padding: 19px 0;
    border-radius: 5px;
    margin-bottom: 5px;
}

.logout span {
    margin-left: 20px;
}

.logout:hover{
    color: #FFF;
}

p{
    margin-bottom: 0px;
}

@media screen and (max-width: 1260px) {
    nav{
        max-width: 220px;
    }
}